/*
*siteSearch 标准地名搜索页
*/

<template>
  <div class="editWrapper">
    <!--编辑框-->
    <edit-box  class="leftView"></edit-box>
    <!--导入导出-->
    <div class="rightBox flex_row" @click="beforeUp">
      <Upload :action="upLoadUrl" class="flex_row" ref="upload"
              name="fileName"
              accept="file"
              :before-upload="beforeUpload"
              :on-success="onUpSuccess"
              :on-error="onUpError"
              :show-upload-list="false"
      >
        <Button  class="item" icon="md-arrow-round-up" >导入</Button>
      </Upload>

      <div class="item flex_col">
        <Button icon="md-download" @click="toggleFix(2)">导出</Button>
      </div>

      <div class="item flex_col">
        <Button icon="md-settings" @click="toggleFix(1)">设置</Button>
      </div>

    </div>
    <Spin fix style="background: rgba(255,255,255,.5);" v-if="showLoading">
      <loading msg="上传中……"></loading>
    </Spin>

    <!--设置列表-->
    <div class="tools" v-if="openTarget">
      <span class="arrow"></span>
      <RadioGroup v-model="targetData" vertical @on-change="changeTarget" style="width: 100%">
        <Radio :label="t" v-for="t in targetList" class="flex_row ellipsis" style="width: 100%"
               :title="t"></Radio>
        <div v-if="targetList.length==0" style="color:#969696">可下载的数据文件</div>
      </RadioGroup>
    </div>
  </div>
</template>

<script>
  import editBox from '../components/editBox.vue'
  import loading from '../components/loading'
  import { mapGetters,mapActions } from 'vuex'
export default {
  name: 'home',
  components: {
    editBox,loading
  },
  created(){
  },
  data () {
    return {
      upLoadUrl:EDITSERVICE['uploadUrl'],//导入地址
      showLoading:false,
      disableUp:false,

      openTarget:false,//打开设置列表
      targetData:null,//目标数据库
      targetList:[],//
      targetListIndex:0, //1为设置，2为导出
    }
  },
  computed:{
    ...mapGetters(['username'])
  },
  methods:{
    ...mapActions( //mapActions需要写在methods里面
      ['updateChangeToEdit']),// 取得这些方法
    //导入前，判断用户是否登录
    beforeUp(){
      if(this.username){
        this.disableUp = false
      }else{
        this.disableUp = true;
      }
    },
    //导入前
    beforeUpload(file){
      var $this = this;
      if($this.disableUp){
        this.$Message.warning('请先登录');
        return false
      }else{
        var nameArr=file.name.split('.');
        /*if(nameArr[nameArr.length-1]=='xls'||nameArr[nameArr.length-1]=='xlsx'){
          //判断上传文件的类型是excel后缀
          $this.showLoading=true
          //清空上传列表
          $this.$refs.upload.clearFiles();
        }else{
          $this.$Modal.warning({
            title: '上传文件后缀应为.xls或.xlsx!',
            width:320
          });
          return false
        }*/
      }


    },
    //导入成功
    onUpSuccess(res, file, fileList) {
      this.$Message.success('导入成功!');
      this.showLoading=false;
      this.$api.initialUrl(this.usage).then(function (res) {
        this.updateChangeToEdit(true);
      })
    },
    //导入失败
    onUpError(error, file, fileList){
      this.showLoading=false;
      this.$Message.error( '导入失败，请重试！');

    },

    //打开设置框
    toggleFix(index){
      if(this.username){
        //调用接口
        var $this = this;
        $this.targetData = null;
        $this.targetListIndex = index;
        $this.$api.getTargetList().then(function (res) {
          if(res){
            $this.openTarget=true;
            $this.targetList = res;
          }else{
            $this.$Message.error( '获取可下载的数据文件失败');
            $this.openTarget=true;
            $this.targetList = [];
          }
        });
      }else{
        this.$Message.warning('请先登录')
      }

    },
    //设置
    changeTarget(val){
      if(val){
        var $this = this;
        if($this.targetListIndex==1){
          $this.$api.setTarget(val).then(function (res) {
            $this.$Message.success('设置成功！')
          }).catch(function (err) {
            if(err.response.data){
              $this.$Message.warning(err.response.data)
            }
          })
        }else{
          $this.$api.exportFile(val).then(function (res) {
            if(res){
              $this.$Message.warning('正在创建下载链接，请稍后……');
              window.open(res)
            }
          }).catch(function (err) {
            if(err.response.data){
              $this.$Message.warning(err.response.data)
            }
          })
        }

      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.editWrapper{
  width: 100%;
  height: 100%;
  overflow-y: hidden;
}
.leftView {
  position: absolute;
  left: 20px;
  z-index: 100;
}
.rightBox{
  position: absolute;
  z-index: 100;
  right: 80px;
  top: 29px;
}
.rightBox .item{
  margin-right: 5px;
  box-shadow: 1px 2px 1px rgba(0,0,0,.15);
  border-radius: 2px;
  background: #fff;
}

  /*设置列表*/
.tools{
  background: #fff;
  padding: 2px 10px;
  box-shadow: 1px 2px 1px rgba(0,0,0,.15);
  border-radius: 2px;
  position: absolute;
  top: 75px;
  right: 85px;
  width: 155px;
  z-index: 100;
}
.arrow {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 7px 8px;
  border-color: transparent transparent #fff;
  position: absolute;
  top: -8px;
  right: 10px;
}
</style>



